import React from 'react'
import Loadable from 'react-loadable'
import scriptjs from 'scriptjs'
import l from './helpers'

const Loader = (props) => (<div className={`vto__loader vto__loader--${props.type}`}/>)

const Makeup = (store) => Loadable.Map({
  modules: ['./Makeup'],
  loader: {
    Makeup: () => import('../Components/Makeup'),
    YMK: () => new Promise(resolve => scriptjs(window.config.vto.makeup.sdk, () => {
      resolve(window.YMK)
      store.dispatch({
        type: 'SDK_LOADED'
      })
    })),
    variants: () => new Promise((resolve, reject) => {
      let list = document.querySelectorAll('.ul-shade-list li'),
        data = []

      if (list && list.length) {
        for (let li of list) {
          let btn = li.querySelector('button'),
            img = btn.querySelector('.button__wrapper img')
          data.push({
            selected: li.classList.contains('selected'),
            code: btn.dataset.value,
            text: btn.querySelector('.button__wrapper span').innerHTML,
            shade: {
              src: img.dataset.src,
              srcSet: img.dataset.srcset
            }
          })
        }
      }

      resolve(data)
    })
  },
  loading: (props) => <Loader type="makeup" {...props} />,
  render: (loaded, props) => {
    let M = loaded.Makeup.default,
      sdk = loaded.YMK,
      compatible = l.vtoSupported('makeup')
    return <M {...props} variants={loaded.variants} compatible={compatible} sdk={sdk}/>
  }
})

export const determineVtoType = (store, product) => {
  if (product.vto && product.vto.category === 'makeup') return Makeup(store)
  return null
}
