import { combineReducers } from 'redux'
import { ACTIONS } from "../../../config/constants"

const productReducer = (state = {
  basic: {},
  vto: {},
  stock: {},
  variants: []
}, {type, ...payload}) => {
  switch (type) {
    case ACTIONS.PRODUCT_CHANGE:
      return payload
    default:
      return state
  }
}
const ready = (state = false, {type}) => {
  switch (type) {
    case ACTIONS.VTO_READY:
      return true
    case ACTIONS.VTO_CLOSE:
      return false
    default:
      return state
  }
}
const showing = (state = false, {type}) => {
  switch (type) {
    case ACTIONS.VTO_OPEN:
      return true
    case ACTIONS.VTO_CLOSE:
      return false
    default:
      return state
  }
}
const overlay = (state = true, {type}) => type === ACTIONS.REMOVE_OVERLAY ? false : state
const sdk_loaded = (state = false, {type}) => {
  switch (type) {
    case ACTIONS.SDK_LOADED:
      return true
    case ACTIONS.SDK_CLOSED:
      return false
    default:
      return state
  }
}
const error = (state = {}, {type}) => {
  switch (type) {
    case ACTIONS.ERROR:
      return {
        name: window.config.vto.labels.reducerErrorName,
        message: window.config.vto.labels.error
      }
    case ACTIONS.CameraNotAvailable:
      return {
        name: window.config.vto.labels.deniedTitle,
        message: window.config.vto.labels.deniedMessage,
      }
    case ACTIONS.VTO_READY:
    case ACTIONS.VTO_CLOSE:
      return {}
    case ACTIONS.CAMERA_ERROR_RESOLVED:
      return {
        message: ''
      }
    default:
      break
  }

  return state
}

const orientation = (state = {portrait: /portrait/.test(screen.orientation.type)}, {type}) => type === ACTIONS.ORIENTATION_CHANGE ? {portrait: window.innerHeight > window.innerWidth} : state

const rendering = (state = false, {type}) => {
  if (type === ACTIONS.START_RENDERING) return true
  if (type === ACTIONS.STOP_RENDERING) return false
  return state
}
const layout = (state = 'desktop', action) => {
  if (action.type === ACTIONS.LAYOUT_CHANGE) {
    return action.payload
  }
  return state
}
const vtoClosed = (state = false, {type}) => {
  if (type === ACTIONS.VTO_CLOSE_CLICK) {
    return true
  }
  return state
}
const vtoVideoStart = (state = false, {type}) => {
  switch (type) {
    case ACTIONS.VTO_VIDEO_START:
      return true
      break
    case ACTIONS.VTO_VIDEO_STOP:
      return false
      break
    default:
      return state
  }
}

const reducers = combineReducers({
  showing,
  rendering,
  layout,
  overlay,
  product: productReducer,
  ready,
  sdk_loaded,
  orientation,
  error,
  vtoClosed,
  vtoVideoStart
})


export default reducers
