import React, { Component, Suspense } from 'react'
import { connect } from 'react-redux'
import { debounce } from "lodash"
import { Manager, VTO_PDP_STYLE_UPDATE } from 'lib/events'
import { isiOS } from 'lib/helpers'

export const VTOWrapper = connect(state => state)(class extends Component {
  displayName = 'VTO Wrapper'

  componentDidMount() {
    window.addEventListener('resize', debounce(() => {
        Manager.publish(VTO_PDP_STYLE_UPDATE)
      }, 50)
    )
  }

  componentWillUnmount() {
    window.removeEventListener('resize', null)
  }

  render() {
    const {product, showing, Viewer} = this.props
    if (!showing) {
      return null
    }
    const productType = product?.vto?.category || ''

    const Loader = (<div className={`vto__loader vto__loader--${productType}`}/>)

    return (
      <div
        className={`vto__container--inner ${productType}  ${isiOS() && 'ios' || ''}`}>
        {productType == 'eyewear' ?
          <Suspense fallback={Loader}>
            <Viewer {...this.props} />
          </Suspense>
          :
          <Viewer {...this.props} />
        }
      </div>
    )
  }
})

export default {VTOWrapper}
