import React, { Component } from 'react'
import { connect } from 'react-redux'
import isMobile from '../../../lib/helpers/mobile-detect'
import { getBrowserInfo, getCurrentMarket } from '../../../lib/helpers'
import get from 'lodash/get'
import { onVtoOpen } from '../Utils/analytics'
import Icon from './Icon'
import l from '../Utils/helpers'
import { vtoOpenAction } from '../Redux/actions'
import { select } from 'lib/selectors'
import { Manager, WINDOW_WAS_RESIZED } from 'lib/events'
import { bindTryOnButtonEvents } from 'ui/try-on-legal'
const browser = getBrowserInfo()

export default connect(state => state)(class Button extends Component {
  state = {
    showCTA: true
  }

  constructor(props){
    super(props)
    this.vtoWrapper = null
    this.nextChevron = select('.product-details .js-slide:first-child a', select('main'))
  }

  async componentWillMount() {
    const {navigatorName = '', navigatorVersion = 0} = l.getDeviceInfos()
    const isMakeupPage = !!select('.page-productDetails-Makeup')

    await new Promise(resolve => {
      if (navigatorName.toLowerCase() === 'ie') {
        this.setState({showCTA: false})
      } else if (isMakeupPage && isMobile())
        if ((navigatorName.toLowerCase() === 'safari' && navigatorVersion < 11) || (navigatorName.toLowerCase() === 'chrome' && navigatorVersion < 60)) {
          this.setState({showCTA: false})
        }

      resolve()
    })
  }

  componentDidMount() {
    if (config.axisType == 'makeup' && true) {
      jsConfigSecond.tryOnLegal.cto.isActive && bindTryOnButtonEvents(this.button,  jsConfigSecond.tryOnLegal.cto.message )
    } else if (config.axisType == 'eyewear' && true) {
      jsConfigSecond.tryOnLegal.vto.isActive && bindTryOnButtonEvents(this.button,  jsConfigSecond.tryOnLegal.vto.message )
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const vtoWrapper = select('.vto__wrapper', select('.vto__container'))

    if( vtoWrapper ) {
      this.vtoWrapper = vtoWrapper
      this.setChevronPosition(vtoWrapper)
    }
  }

  openVto() {
    const {dispatch, product} = this.props
    onVtoOpen(product.code) // analytics
    vtoOpenAction({dispatch})

    if (!isMobile())
      window.scrollTo({
        top: 0,
        left: 0,
        options: {
          behavior: 'smooth'
        }
      })

    /* On some tablets, on portrait position with VTO started there is no chevron.
        But with landscape position, there is a chevron. So, we must detect orientation change
     */
    Manager.subscribe(WINDOW_WAS_RESIZED, () => this.setChevronPosition(this.vtoWrapper))
  }

  setChevronPosition = (vtoWrapper) => {
    if (this.nextChevron && vtoWrapper) {
      this.nextChevron.style.top = `calc(50% + ${parseInt(window.getComputedStyle(vtoWrapper).height) / 2}px)`
      this.nextChevron.style.zIndex = '2'
    }
  }

  isPDP() {
    return document.body.classList.contains('pageType-ProductPage')
  }

  isApplePayPDP() {
    const applePayPDPBtn = select('li.js-apple-pay-btn')
    if (applePayPDPBtn) return !applePayPDPBtn.hasAttribute('hidden')
  }

  render() {
    let buttonProps
    const {product, labels, showing, onMouseOver, ready, sdk_loaded, vtoClosed, orientation} = this.props,
      openInApp = isMobile() && !(/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream) && product.vto && product.vto.category === 'eyewear',
      defaultAriaLabel = `${labels.tryOn} ${get(product, 'basic.name', '')}`,
      {showCTA} = this.state,
      productRef = get(product, 'baseProduct', '')
    
    if(window.jsConfigSecond.isVTOActive) {
      buttonProps = {
        'type':'button',
        className: `button ${!this.isApplePayPDP() ? 'is-secondary is-fullwidth js-vto-button' : ' is-tertiary js-vto-button '} vto-open  ${config.axisType} ${showing || !ready ? 'disabled' : ''} ${showCTA ? '' : 'is-hidden'}`,
        'aria-label': defaultAriaLabel,
        'data-vto-ref': productRef,
        'data-defaultname': product.basic && product.basic.defaultName || '',
        'data-vto-view': `${this.isPDP() ? 'single' : 'compare'}`,
        'data-test': 'btnVirtualTryOn',
        onMouseOver
      }
    } else {
      buttonProps = {
        'type':'button',
        className: `button ${!this.isApplePayPDP() ? 'is-secondary is-fullwidth' : ' is-tertiary js-vto-open '} vto-open ${config.axisType} ${showing || !ready ? 'disabled' : ''} ${showCTA ? '' : 'is-hidden'}`,
        'aria-label': defaultAriaLabel,
        'data-defaultname': product.basic && product.basic.defaultName || '',
        'data-test': 'btnVirtualTryOn',
        onMouseOver
      }
    }

    let text = labels.tryOn
    let hasIcon = true
    let hasApplePay = this.isApplePayPDP()

    if(!window.jsConfigSecond.isVTOActive) {
      buttonProps.onClick = () => this.openVto()
    }
    
    if (!sdk_loaded) buttonProps.disabled = false

    if (showing) {
      buttonProps.disabled = true
      buttonProps.className += ' buttonDisabled'
    }
    if (openInApp) {
      buttonProps.role = 'link'
      buttonProps['aria-label'] = `Chanel App - ${defaultAriaLabel}`
    }

    if (vtoClosed) {
      setTimeout(() => this.button.focus(), 50)
    }

    return (
      <button ref={node => this.button = node} {...buttonProps}>
       {!hasApplePay ? <span className="button__wrapper">
        {hasIcon ? <span className="button__icon">
           <Icon className="is-large is-webcam" name="webcam"/>
          </span> : null}
            <span>{text}</span>
          </span> : <span> {hasIcon ? <span>
         <Icon className="is-medium is-webcam" name="webcam"/> {text}
        </span> : null}</span>
       }
      </button>
    )
  }
})
