import { onVtoClosed } from "../Utils/analytics"
import { ACTIONS } from "../../../config/constants"

import { Manager, VTO_PDP_STYLE_UPDATE } from 'lib/events'

export const cameraErrorResolvedAction = (store) => store.dispatch({
  type: ACTIONS.CAMERA_ERROR_RESOLVED
})

export const vtoVideoStartAction = (store) => store.dispatch({
  type: ACTIONS.VTO_VIDEO_START
})

export const vtoVideoStopAction = (store) => store.dispatch({
  type: ACTIONS.VTO_VIDEO_STOP
})


export const vtoCameraOpenAction = (store) => store.dispatch({
  type: ACTIONS.VTO_BTN_CLICKED,
})

export const vtoCameraStoppedAction = (store) => store.dispatch({
  type: ACTIONS.VTO_CAMERA_STOPPED
})

export const vtoReadyAction = (store) => store.dispatch({
  type: ACTIONS.VTO_READY,
  category: 'eyewear'
})

export const vtoCloseAction = (store, window) => {
  store.dispatch({
    type: ACTIONS.VTO_CLOSE,
  })

  if (window.vtoStream) {
    window.vtoStream.getTracks()
      .forEach(track => track.stop())
    window.vtoStream = null
    delete window.vtoStream
  }

  onVtoClosed(store.getState().product.code)

  Manager.publish(VTO_PDP_STYLE_UPDATE)
}

export const layoutChangeAction = (store, type) => store.dispatch({
  type: ACTIONS.LAYOUT_CHANGE,
  payload: type
})

export const productChangeAction = (store, product) => store.dispatch({
  type: ACTIONS.PRODUCT_CHANGE,
  ...product
})

export const vtoOpenAction = (store) => {
  store.dispatch({
    type: ACTIONS.VTO_OPEN
  })
}

export const orientationChangeAction = (store, event) => store.dispatch({
  type: ACTIONS.ORIENTATION_CHANGE,
  payload: event ? event.target.screen.orientation.angle : 0
})

export const vtoCloseClickAction = (store) => store.dispatch({
  type: ACTIONS.VTO_CLOSE_CLICK
})

export const errorAction = (store, err) => store.dispatch({
  type: ACTIONS.ERROR,
  err
})

export const vtoCameraNotAvailableAction = (store) => store.dispatch({
  type: ACTIONS.CameraNotAvailable
})

export const vtoSDKCloseAction = (store) => store.dispatch({
  type: ACTIONS.SDK_CLOSED
})
