import React from 'react'
import 'webrtc-adapter'
import 'lib/polyfills/orientation'
import { render } from 'react-dom'
import { Provider } from 'react-redux'
import { VTOWrapper } from './Components/VtoWrapper'
import Button from './Components/Button'
import isMobile from '../../lib/helpers/mobile-detect'
import {onChangeModel, onVtoOpen } from './Utils/analytics'
import l from './Utils/helpers'
import { determineVtoType } from './Utils/determineVtoType'
import configureStore from './Redux/configureStore'
import {
  layoutChangeAction,
  orientationChangeAction,
  productChangeAction,
  vtoCloseAction,
  vtoOpenAction,
  vtoReadyAction
} from './Redux/actions'

import checkForCamera from 'modules/camera'
import { select } from 'lib/selectors'

const store = configureStore()
let isForcedClosed = false

checkForCamera()

store.subscribe(() => {
  const state = store.getState()

  if (state.showing && state.product && state.product.vto && state.product.vto.vtoEnabled || isForcedClosed) {
    document.body.classList.add(`vto-${state.product.vto.category || window.fallbackVTOCategory || window.config.axisType}`)
  } else {
    document.body.classList.remove(`vto-${state.product.vto.category || window.fallbackVTOCategory}`)
  }
})

window.startCapture = () => {
  vtoReadyAction(store)
}
window.vtoClose = (forcedClosed = false) => {
  vtoCloseAction(store, window)
  window.requestAnimationFrame(() => {
    isForcedClosed = forcedClosed
    if (forcedClosed) {
      vtoOpenAction(store, window)
    }
  })
}
window.renderVTO = renderVTO

if (isMobile()) layoutChangeAction(store, 'mobile')

/**
 *
 * @param product
 * @param options
 *
 * options: {
 *     ctaContainer: function
 *     vtoContainer: function || DOMElement,
 *     autostart: boolean
 * }
 */
function renderVTO(product, options = {}) {
  productChangeAction(store, product)

  const Viewer = product.vto.category == 'eyewear' ? React.lazy(() => import('./Components/Eyewear')) :  determineVtoType(store, product)

  const preloadAll = () => {
    if (Viewer) {
      const start = new Date().getTime()
      return Viewer.preload()
        .then(() => {
          const end = new Date().getTime()
          if (process.env.NODE_ENV === 'development') {
            console.time('preloaded sdk! took ' + (end - start) + 'ms')
          }
        })
        .catch(e => {
          console.error('error preloading framework.', e)
        })
    }
  }

  const showing = store.getState().showing

  if (product.vto.vtoEnabled) {

    if(!(product.vto.category == 'eyewear')) {
      preloadAll()
    }

    const p = document.createElement('p')
    p.setAttribute('class', 'vto-open__wrapper')

    const facecakeVTOWrapper = document.createElement('p')
    facecakeVTOWrapper.setAttribute('class', 'vto-open__wrapper facecake_CTA')

    if (options.ctaContainer) {
      if (options.ctaContainer() instanceof HTMLElement) {

        // preload on mouse over the button to speed it up
        if (product.vto.category === 'eyewear') {
          if (l.vtoSupported(product.vto.category || window.fallbackVTOCategory)) {
            options.ctaContainer()
              .appendChild(facecakeVTOWrapper)

            render(
              <Provider store={store}>
                <Button
                  labels={options.labels}
                  compatible={l.vtoSupported(product.vto.category || window.fallbackVTOCategory)}
                />
              </Provider>,
              facecakeVTOWrapper
            )
          }
        } else {

          options.ctaContainer()
            .appendChild(p)

          render(
            <Provider store={store}>
              <Button
                labels={options.labels}
                compatible={l.vtoSupported(product.vto.category || window.fallbackVTOCategory)}
              />
            </Provider>,
            p
          )
        }
      }
    }
  }

  if (!showing) {
    render(
      <Provider store={store}>
        <VTOWrapper labels={options.labels} Viewer={Viewer} facecakeProps={window.config.facecakeProps}/>
      </Provider>, typeof options.vtoContainer === 'function' ? options.vtoContainer() : options.vtoContainer
    )
  } else {
    onChangeModel(store.getState().product.code)
  }

  if (options.autostart) {
    if (window.jsConfigSecond.isVTOActive){
      const tryOnCTA = document.querySelector('.product-details__ctas .js-vto-button')
      if (tryOnCTA && window.isFshVtoScriptLoaded && product.vto.category === 'eyewear') {
        tryOnCTA.click()
      }
    } else {
      const tryOnCTA = select('.product-details__container .vto-open')
      tryOnCTA.click()
    }
    window.localStorage.removeItem('vtoOpen')
  }

  window.addEventListener('orientationchange', (e) => {
    orientationChangeAction(store, e)
  })
}

