export default function () {

    if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
        console.log("enumerateDevices() not supported.");
        //return;
    }

    // List cameras and microphones.

    if (navigator.mediaDevices) {
      navigator.mediaDevices.enumerateDevices()
          .then(function (devices) {

              let exists = false

              devices.forEach(function (device) {
                  exists |= device.kind === 'videoinput'
              });

              if(exists) {
                  console.log('Camera detected. vto can be enabled on this device')
                  document.body.classList.add('camera-ok');
              }else {
                  console.log('Camera undetected. vto is disabled on this device')
                  document.body.classList.add('no-camera');
              }
          })
          .catch(function (err) {
              console.log(err.name + ": " + err.message);
          });
    }
}